import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import editBuyPage from './views/editBuyPage.vue'
import usersAddress from './components/usersAddress.vue'
import record from './views/record.vue'
import payPage from './views/payPage.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/edit',
      name: 'editBuyPage',
      component: editBuyPage,
      props: (route) => ({ id: route.query.id, count: route.query.id })
    },
    {
      path: '/uad',
      name: 'usersAddress',
      component: usersAddress
    },
    {
      path: '/record', // record
      name: 'record',
      component: record
    },
    {
      path: '/paypage', // record
      name: 'payPage',
      component: payPage
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
