<template>
  <div class="edit">
    <div class="user-area border-bottom-white" @click="addressShow = !mode">
      <div v-if="!mode">
        <p class="area" v-if="inputData === null">请填写配送信息</p>
        <div v-else>
          <p class="user">{{`${inputData.receiver_name} ${inputData.mobile}`}}</p>
          <p class="user-area-p">{{`${inputData.province}-${inputData.city}-${inputData.area}-${inputData.address}`}}</p>
        </div>
        <p class="right-icon">></p>
      </div>
      <div v-else>
          <p class="user">{{modeData.users}}</p>
          <p class="user-area-p">{{modeData.address}}</p>
      </div>
    </div>

    <div class="pay-way border-bottom-white">
      <div class="weixin" @click="changPayWay(true)">
        <div class="icon"><img src="../assets/social_wechat_active.png"/>微信</div>
        <img class="pick" v-if="weixin" src="../assets/check_active.png"/>
        <img v-else src="../assets/check_default.png"/>
      </div>
      <div class="ali" @click="changPayWay(false)">
        <div class="icon"><img src="../assets/social_alipay.png"/>支付宝</div>
        <img class="pick" v-if="!weixin" src="../assets/check_active.png"/>
        <img v-else src="../assets/check_default.png" />
      </div>
    </div>

    <div class="goods-template border-bottom-white" v-if="goods">
      <img class="goods-img" v-if="goods.kind_id >= 1 && goods.kind_id <= 3" src="../assets/shop_pic_yobuhome_01@2x.png"/>
      <img class="goods-img" v-if="goods.kind_id >= 4 && goods.kind_id <= 6" src="../assets/shop_pic_yobuhome_03@2x.png"/>
      <img class="goods-img" v-if="goods.kind_id >= 7 && goods.kind_id <= 9" src="../assets/shop_pic_yobuhome_05@2x.png"/>
      <div class="goods-desc" v-if="!mode">
        <p class="name">小睡眠豪华舒适四件套</p>
        <div class="desc" v-if="goods">{{goods.kind_style}}<span class="count">x{{count}}</span></div>
        <p class="price" v-if="goods">￥{{this.$root.vip === true ? goods.kind_price_vip : goods.kind_price}}</p>
      </div>
      <div class="goods-desc" v-if="mode">
        <p class="name">小睡眠豪华舒适四件套</p>
        <div class="desc">{{modeData.kind_style}}<span class="count">x{{modeData.count}}</span></div>
        <p class="price">￥{{modeData.price}}</p>
      </div>
    </div>

    <div class="common-template-evenly border-bottom-white">
      <p class="desc">商品金额</p>
      <p class="price" v-if="mode" style="font-family: 'br'">￥{{modeData.price}}</p>
      <p class="price" v-if="!mode && goods" style="font-family: 'br'">￥{{(this.$root.vip === true ? goods.kind_price_vip : goods.kind_price) * count}}</p>
    </div>
    <div class="common-template-evenly">
      <p class="desc">运费</p>
      <p class="price" style="color: #000">免邮</p>
    </div>

    <div class="meain-pad-color"></div>

    <div class="button-template">
      <div class="icon-template" @click="back">
        <img class="icon" src="../assets/icon/order.png"/>
        取消
      </div>
      <p class="price" v-if="goods">￥{{(this.$root.vip === true ? goods.kind_price_vip : goods.kind_price) * count}}</p>
      <div class="button" @click="createAOrder" :class="{'background-blue': inputData || mode}">前往支付</div>
    </div>

    <Address v-show="addressShow" class="address" />
  </div>
</template>

<script>
import Address from '../components/usersAddress.vue'

export default {
  name: 'editBuyPage',
  data () {
    return {
      weixin: true, // 默认微信支付
      // 填写地址的模板是否展示 广东省广州市海珠区新港东路1226号C塔14层
      addressShow: false,
      goods: null,
      count: null,
      inputData: null,
      mode: false,
      orderNumber: null,
      modeData: {
        users: '',
        address: '',
        kind_style: '',
        price: '',
        count: '',
        orderNumber: ''
      }
    }
  },
  components: {
    Address
  },
  methods: {
    changPayWay (b) {
      if (b) {
        this.weixin = true
        _hmt.push(['_trackEvent', 'info', 'pay', `wechat`])
      }  else {
        this.weixin = false
        _hmt.push(['_trackEvent', 'info', 'pay', `alipay`])
      }
    },
    // 继续支付
    againPay () {
      // window.location.href.split('#')[0] + '/#/paypage?order_number=' + this.modeData.orderNumber
      this.$axios.post('/web/v1/cosleep/entitygoods/order/repay', { order_number: this.modeData.orderNumber, return_url: window.location.href.split('?')[0] + '/#/record' }).then(res => {
        if (res.data.status === 1) {
          this.weixin === true
            ? window.location.href = res.data.data.pay_platforms.wechatpay.mweb_url // + '&redirect_url=' + encodeURIComponent(window.location.href.split('#')[0] + '/#/paypage?order_number=' + res.data.data.order_number)
            : window.location.href = res.data.data.pay_platforms.alipay
          this.$parent.againPay = false
          // this.$parent.showTips('支付成功')
          // this.weixin !== true
          // this.weixin !== true &&
          this.$router.push('/paypage?order_number=' + res.data.data.order_number)
          // this.$router.push('/paypage?order_number=' + res.data.data.order_number)
        }
      })
    },
    createAOrder () {
      console.log(
        window.location.href.split('?')[0] + '/#/paypage?order_number=' + this.modeData.orderNumber
      )
      if (this.mode) {
        this.againPay()
        return
      }
      if (!this.inputData) {
        this.$parent.showTips('请输入配送信息')
        return
      }
      _hmt.push(['_trackEvent', 'info', 'pay', `next`])
      const data = {
        kind_id: this.goods.kind_id,
        order_goods_count: this.count,
        order_user_name: this.inputData.receiver_name,
        order_user_mobile: this.inputData.mobile,
        order_address: `${this.inputData.province}-${this.inputData.city}-${this.inputData.area}-${this.inputData.address}`,
        return_url: window.location.href.split('?')[0] + '/#/record' // 接口矛盾
      }
      this.$axios({
        url: '/web/v1/cosleep/entitygoods/order',
        method: 'POST',
        data,
        headers: {
          'openid': this.$root.openid
        }
      }).then(res => {
        const status = res.data.status
        if (status === 1) {
          this.weixin === true
            ? window.location.href = res.data.data.pay_platforms.wechatpay.mweb_url // + '&redirect_url=' + encodeURIComponent(window.location.href.split('#')[0] + '/#/paypage?order_number=' + res.data.data.order_number)
            : window.location.href = res.data.data.pay_platforms.alipay
          // this.$parent.showTips('支付成功')
          // this.weixin !== true &&
          // this.weixin !== true &&
          this.$router.push('/paypage?order_number=' + res.data.data.order_number)
        }
      })
    },
    back () {
      if (this.mode) {
        this.$parent.againPay = false
        return
      }
      _hmt.push(['_trackEvent', 'info', 'pay', `cancel`])
      this.$router.replace('/')
    }
  },
  mounted () {
    if (this.mode) {
      return
    }
    const id = +this.$route.query.id || +localStorage.getItem('id')
    const count = +this.$route.query.count || +localStorage.getItem('count')
    if (id && count) {
      localStorage.setItem('id', id)
      localStorage.setItem('count', count)
      this.count = count
      this.$root.showLoading = true
      this.$axios.get('/web/v1/cosleep/entitygoods/kind', { params: { goods_id: this.$root.goods_id } }).then(res => {
        this.$root.showLoading = false
        if (res.data.status !== 1) {
          // console.log('网络错误')
          return
        }
        console.log(res.data.data)
        const data = res.data.data
        for (let i of data) {
          if (i.kind_id === id) {
            this.goods = i
            console.log(this.goods)
            return
          }
        }
      })
    } else {
      // this.$router.push('/')
    }
  }
}
</script>

<style lang="scss" scoped src="./editBuyPage.scss">
</style>
