<template>
  <div class="record">
    <div class="warn-template" v-if="goodsListData.length === 0">
      <img class="desc-img" src="../assets/img_enpty@2x.png">
      <p class="desc">暂无订单</p>
    </div>
    <div v-for="(li, index) in goodsListData" :key="index" class="order-template border-bottom-white">
      <div class="one border-bottom-white">
        <p class="time">{{parseTime(li.created_at)}} </p>
        <p class="status" :class="{'change-color': li.order_status === 4}">{{status[li.order_status]}}</p>
      </div>
      <div class="goods-template border-bottom-white">
        <img class="goods-img" v-if="li.order_kind_info.kind_id >= 1 && li.order_kind_info.kind_id <= 3" src="../assets/shop_pic_yobuhome_01@2x.png"/>
        <img class="goods-img" v-if="li.order_kind_info.kind_id >= 4 && li.order_kind_info.kind_id <= 6" src="../assets/shop_pic_yobuhome_03@2x.png"/>
        <img class="goods-img" v-if="li.order_kind_info.kind_id >= 7 && li.order_kind_info.kind_id <= 9" src="../assets/shop_pic_yobuhome_05@2x.png"/>
        <div class="goods-desc">
          <p class="name">{{li.order_goods_info.goods_name}}</p>
          <div class="desc">{{li.order_kind_info.kind_style}} {{li.order_kind_info.kind_size}}<span class="count">x{{li.order_goods_count}}</span></div>
          <p class="price">￥{{li.order_kind_info.kind_price}}</p>
        </div>
        <img v-show="li.order_status === 3" src="../assets/cosleep_point_img_complete@2x.png" class="success-img"/>
      </div>
      <div class="three">
        <p class="all-desc">共{{li.order_goods_count}}件商品，总金额</p>
        <p class="price">￥{{li.order_price}}</p>
      </div>
      <div class="five">
        <div class="five-button" v-if="li.order_status === 4" @click="cancleOrder">删除订单</div>
        <div class="five-button" v-if="li.order_status === 4 || li.order_status === 3 || li.order_status === 2" @click="cancleOrder">重新下单</div>
        <div class="five-button" v-if="li.order_status === 0" @click="againPayMethod(li.order_user_name + ' ' + li.order_user_mobile, li.order_address, li.order_kind_info.kind_style + ' ' + li.order_kind_info.kind_size, li.order_price, li.order_goods_count, li.order_number)">支付订单</div>
      </div>
    </div>
    <editBuyPage ref="editBuyPage" v-if="againPay" class="editBuyPage"/>
    <div class="bottom-template">
      <div class="button" @click="back">返回</div>
    </div>
  </div>
</template>

<script>
import editBuyPage from '../views/editBuyPage.vue'
export default {
  name: 'record',
  components: {
    editBuyPage
  },
  data () {
    return {
      status: ['等待支付', '买家已付款', '订单已发货', '订单已完成', '订单已失效'],
      goodsListData: [],
      againPay: false
    }
  },
  methods: {
    // 取消订单
    cancleOrder (orderNumber, index) {
      this.$axios.post('/web/v1/cosleep/entitygoods/order/cancel', { order_number: orderNumber }).then(res => {
        if (res.data.status === 1) {
          this.goodsListData.splice(index, 1)
          this.$parent.showTips('取消订单成功')
        }
      })
    },
    // 继续支付订单
    againPayMethod (users, address, kindStyle, price, count, orderNumber) {
      this.againPay = true
      this.$nextTick(() => {
        this.$refs['editBuyPage'].mode = true
        this.$refs['editBuyPage'].modeData = {
          users,
          address,
          kind_style: kindStyle,
          price,
          count,
          orderNumber
        }
      })
    },
    parseTime (s) {
      return this.WJH.dateFormat(+(s + '000'), 'yyyy-MM-dd hh:mm')
    },
    getData () {
      this.$root.showLoading = true
      this.$axios.get('/web/v1/cosleep/entitygoods/history', {
        headers: { 'openid': localStorage.getItem('openid') } // 设置header信息
      }).then(res => {
        if (res.data.status === 1) {
          this.goodsListData = res.data.data
          this.$root.showLoading = false
        }
      })
    },
    back () {
      this.$router.replace('/')
    }
  },
  mounted () {
    this.getData()
    _hmt.push(['_trackEvent', 'check', 'click', `shop`])
  }
}
</script>

