<template>
  <div class="users-address">
    <div class="input-template">
      <p class="title">收件人</p>
      <input placeholder="（必填）" onkeyup="this.value=this.value.replace(/\s+/g,'')" v-model="inputData.receiver_name" class="input" />
    </div>
    <div class="input-template">
      <p class="title">手机号</p>
      <input placeholder="（必填）" :change="check_num()" v-model="inputData.mobile" class="input" />
    </div>
    <div class="input-template" @click="show = true">
      <p class="title">所在地区</p>
      <p class="address">{{ alst(inputData.province + inputData.city + inputData.area) }}</p>
      <div class="arrow">></div>
    </div>
    <div class="input-template">
      <p class="title">详细地址</p>
      <textarea placeholder="门牌号、小区、单元室等（必填）" v-model="inputData.address" class="input text-area"></textarea>
    </div>

    <div class="meain-pad-color">

    </div>
    <!-- <div class="clasp animated fadeIn" v-if="show">
    </div> -->
    <div class="button-template">
      <div class="button" @click="submitData(inputData.mobile.length === 11 && inputData.city && inputData.address && inputData.receiver_name)" :class="{'background-blue': inputData.mobile.length === 11 && inputData.city && inputData.address && inputData.receiver_name}">确定</div>
    </div>
    <div @click.self="closeArea" style="position: fixed;top: 0px;left:0px; height: 100vh;width: 100vw;background: rgba(0, 0, 0, 0.5)" v-show="show">
      <AreaP class="area" ref="area"></AreaP>
    </div>
  </div>
</template>

<script>
import AreaP from './area.vue'

export default {
  name: 'usersAddress',
  props: {
    msg: String
  },
  data () {
    return {
      show: false,
      inputData: {
        province: '',
        city: '',
        area: '',
        address: '',
        receiver_name: '',
        mobile: ''
      }
    }
  },
  methods: {
    closeArea () {
      this.$refs['area'].close()
    },
    // can though
    submitData (boolean) {
      if (boolean) {
        this.$parent.addressShow = false
        this.$parent.inputData = this.inputData
      } else {
        if (!this.inputData.receiver_name) {
          this.$parent.$parent.showTips('请输入收件人名称')
        }
        if (this.inputData.mobile.length !== 11) {
          this.$parent.$parent.showTips('请输入正确的手机号码')
          return
        }
        if (this.inputData.mobile.length === 0) {
          this.$parent.$parent.showTips('请输入手机号码')
          return
        }
        if (!this.inputData.address) {
          this.$parent.$parent.showTips('请输入详细地址')
          return
        }
        if (!this.inputData.city) {
          this.$parent.$parent.showTips('请选择配送城市和地区')
        }
      }
    },
    check_num () {
      let licenseNum = this.inputData.mobile
      licenseNum = (licenseNum + '').replace(/[^\d]/g, '') // 清除“数字”和“.”以外的字符
      if (licenseNum.indexOf('.') < 0 && licenseNum !== '') {
        // 以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
        licenseNum = parseInt(licenseNum)
      }
      this.inputData.mobile = licenseNum.toString().slice(0, 11)
    },
    alst (desc) {
      /* eslint-disable */
      if (desc.replace(/\s/g, '')) {
        this.changeColor = 'rgba(0, 0, 0, 0.6)'
      }
      return desc.replace(/\s/g, '') ? desc : '选择配送城市和地区'
    }
  },
  components: {
    AreaP
  }
}
</script>

