<template>
  <div class="payPage">
    <div v-if="orderData.order_status === 1" class="top-template change-height">
      <img class="bg" src="../assets/cosleep_shop_bg_order_b@2x.png" >
      <p class="success">支付完成</p>
      <div class="price">
        <span class="unit">￥</span>
        <span>{{orderData.order_price}}</span>
      </div>
      <img class="miao-craw" src="../assets/cosleep_common_img_catfeet@2x.png">
    </div>
    <div v-else class="top-template" :class="{ 'green-background': orderData.order_status === 5 }">
      <img class="bg" src="../assets/cosleep_shop_bg_order_b@2x.png" >
      <p class="wait-for-pay">{{this.$root.changeStatus[orderData.order_status]}}</p>
      <p class="time">
        <span v-if="orderData.order_status === 0" >剩余支付时间：{{createTime}}</span>
        <span v-if="orderData.order_status === 2" >物流：{{orderData.order_delivery}}</span>
      </p>
      <div v-if="orderData.order_status === 0" class="button" @click="againPayMethod">支付订单</div>
      <div v-if="orderData.order_status === 3" class="button" @click="againPayMethod">已完成重新购买</div>
      <div v-if="orderData.order_status === 4" class="button" @click="toHomePage">重新下单</div>
    </div>

    <div class="users-address border-bottom-white">
      <div class="user">{{orderData.order_user_name}} {{orderData.order_user_mobile}}</div>
      <p class="address">{{orderData.order_address}}</p>
    </div>

    <div class="goods-template border-bottom-white" v-if="orderData.order_kind_info">
      <img class="goods-img" v-if="orderData.order_kind_info.kind_id >= 1 && orderData.order_kind_info.kind_id <= 3" src="../assets/shop_pic_yobuhome_01@2x.png"/>
      <img class="goods-img" v-if="orderData.order_kind_info.kind_id >= 4 && orderData.order_kind_info.kind_id <= 6" src="../assets/shop_pic_yobuhome_03@2x.png"/>
      <img class="goods-img" v-if="orderData.order_kind_info.kind_id >= 7 && orderData.order_kind_info.kind_id <= 9" src="../assets/shop_pic_yobuhome_05@2x.png"/>
      <div class="goods-desc" v-if="orderData.order_goods_info">
        <p class="name">{{orderData.order_goods_info.goods_name}}</p>
        <div class="desc">{{orderData.order_kind_info.kind_style}} {{orderData.order_kind_info.kind_size}}<span class="count">x{{orderData.order_goods_count}}</span></div>
        <p class="price">￥{{orderData.order_kind_info.kind_price}}</p>
      </div>
    </div>

    <div class="common-template-evenly">
      <p class="desc">商品金额</p>
      <p class="price">￥{{orderData.order_price}}</p>
    </div>

    <div class="common-template-evenly border-bottom-white">
      <p class="desc">运费</p>
      <p class="price">包邮</p>
    </div>

    <div class="common-template-evenly border-bottom-white">
      <p class="desc" style="color: rgba(0, 0, 0, 0.7)">实际付款</p>
      <p class="price" style="color: rgba(74, 144, 226, 1)">￥{{orderData.order_price}}</p>
    </div>

    <div class="record-desc border-bottom-white">
      <p>订单号：{{orderData.order_number}}</p>
      <p>下单时间：{{parseTime(orderData.created_at)}}</p>
    </div>
    <editBuyPage ref="editBuyPage" v-if="againPay" class="editBuyPage"/>
    <div class="bottom-template" @click="againPayMethod(false)">
      <div class="button">返回</div>
    </div>
    <div class="meain-pad-color" v-if="orderData.order_status !== 1 || orderData.order_status !== 4 || orderData.order_status !== 0">
      <p>请在配送期间保持联系手机号码的畅通，以确保订单送达，如存在地址信息错误或其他疑问，请及时联系小睡眠工作人员QQ1192824746。</p>
    </div>
  </div>
</template>

<script>
import editBuyPage from '../views/editBuyPage.vue'
export default {
  name: 'payPage',
  data () {
    return {
      orderData: {},
      createTime: '',
      orderNumber: '',
      againPay: false
    }
  },
  components: {
    editBuyPage
  },
  methods: {
    // 重新支付
    toHomePage () {
      this.$router.replace('/')
    },
    // 继续支付订单
    againPayMethod (pg = true) {
      if (pg) {
        _hmt.push(['_trackEvent', 'final', 'click', `pay`])
      } else {
        _hmt.push(['_trackEvent', 'final', 'click', `complete`])
      }
      if (this.orderData.order_status === 1) {
        this.$router.replace('/record')
      }
      this.againPay = true
      this.$nextTick(() => {
        console.log(
          this.$refs['editBuyPage']
        )
        this.$refs['editBuyPage'].mode = pg
        // 可以修改地址和用户信息的mode
        this.$refs['editBuyPage'].inputData = {
          receiver_name: this.orderData.order_user_name,
          mobile: this.orderData.order_user_mobile,
          province: this.orderData.order_address.split('-')[0],
          city: this.orderData.order_address.split('-')[1],
          area: this.orderData.order_address.split('-')[2],
          address: this.orderData.order_address.split('-')[3]
        }
        this.$refs['editBuyPage'].modeData = {
          users: this.orderData.order_user_name + ' ' + this.orderData.order_user_mobile,
          address: this.orderData.order_address,
          kind_style: this.orderData.order_kind_info.kind_style + ' ' + this.orderData.order_kind_info.kind_size,
          price: this.orderData.order_kind_info.kind_price,
          count: this.orderData.order_goods_count,
          orderNumber: this.orderData.order_number
        }
      })
    },
    // 秒
    parseTime (s) {
      return this.WJH.dateFormat(+(s + '000'), 'yyyy-MM-dd hh:mm:ss')
    },
    // 获取订单信息
    getData (orderNumber, once = false) {
      this.$axios.get('/web/v1/cosleep/entitygoods/order/detail', { params: { order_number: orderNumber } }).then(res => {
        if (res.data.status === 1) {
          this.orderData = res.data.data
          if (once) {
            return
          }
          let time = res.data.data.created_at - parseInt(Date.parse(new Date()) / 1000) + (15 * 60)
          if (time <= 0) {
            time = 0
            this.createTime = this.WJH.musicCount(time)
            return
          }
          this.createTime = this.WJH.musicCount(time)
          const mark = setInterval(() => {
            time -= 1
            this.createTime = this.WJH.musicCount(time)
            if (time <= 0) {
              clearInterval(mark)
            }
          }, 1000)
        }
      })
    }
  },
  mounted () {
    if (!this.$root.openid) {
      this.$root.getLogin(() => {
        this.orderNumber = this.$route.query.order_number
        if (this.orderNumber) {
          this.getData(this.orderNumber)
          let mark = setInterval(() => {
            this.getData(this.orderNumber, true)
            console.log(this.orderData.order_status)
            if (this.orderData.order_status !== 0) {
              clearInterval(mark)
            }
          }, 3000)
        }
      })
      return
    }
    this.orderNumber = this.$route.query.order_number
    if (this.orderNumber) {
      this.getData(this.orderNumber)
      let mark = setInterval(() => {
        this.getData(this.orderNumber, true)
        console.log(this.orderData.order_status)
        if (this.orderData.order_status !== 0) {
          clearInterval(mark)
        }
      }, 3000)
    }
  }
}
</script>

<style lang="scss" scoped src="./payPage.scss">
</style>
