<template>
  <div class="edit-style">

    <div class="bed-desc" v-if="nowKind.length >= 1">
      <div v-if="nowKind.length >= 1">
        <img class="bed-img" v-if="nowKind[0].kind_id >= 1 && nowKind[0].kind_id <= 3" src="../assets/shop_pic_yobuhome_01@2x.png"/>
      <img class="bed-img" v-if="nowKind[0].kind_id >= 4 && nowKind[0].kind_id <= 6" src="../assets/shop_pic_yobuhome_03@2x.png"/>
        <img class="bed-img" v-if="nowKind[0].kind_id >= 7 && nowKind[0].kind_id <= 9" src="../assets/shop_pic_yobuhome_05@2x.png"/>
      </div>
      <!-- <img v-else class="bed-img" src="../assets/shop_pic_yobuhome_03@2x.png"/> -->
      <div class="desc">
        <p class="price">￥{{this.$root.vip === true ? this.$parent.goodsBaseData.goods_price_vip : this.$parent.goodsBaseData.goods_price}}</p>
        <p class="remain" v-if="select">库存：{{select.kind_total_count}}</p>
        <p class="choice-style">请选择 款式 尺寸</p>
      </div>
    </div>

    <div class="style-template union-id-1">
      <div class="title">款式</div>
      <div class="styles">
        <p class="style" v-for="(li, index) in kinds" :key="index" :class="{'zero-style': li[0].total === 0}" @click="changeKind(li, $event, index)">{{li[0].kind_style}}</p>
        <!-- <p class="style" :class="{'zero-style': true}" @click="changeKind('春日气息：绿色', $event)">春日气息：绿色</p>
        <p class="style" :class="{'zero-style': true}" @click="changeKind('阳光记忆：米黄', $event)">阳光记忆：米黄</p>
        <p class="style" :class="{'zero-style': true}" @click="changeKind('静晓：天蓝', $event)">静晓：天蓝</p> -->
      </div>
    </div>

    <div class="size-template style-template" v-if="nowKind.length >= 1">
      <div class="title">尺寸</div>
      <div class="sizes styles">
        <p class="size style" v-for="(li, index) in nowKind" @click="selectMethod(li, $event, index)" :class="{'zero-style': li.total <= 0}" :key="index">{{ li.kind_size }}</p>
      </div>
    </div>

    <div class="count-template style-template" v-if="select">
      <div class="title">数量</div>
      <div class="counter-template" v-if="select">
        <div class="outer">
          <div class="minus" @click="minus">-</div>
          <div class="csp" v-if="minusDisable"></div>
        </div>
        <div class="number">{{ acceptNumber }}</div>
        <div class="outer">
          <div class="add" @click="add">+</div>
          <div class="csp" v-if="addDisable"></div>
        </div>
      </div>
    </div>

    <div class="button" @click="submitData" :class="{'background-blue': acceptNumber > 0}">确定</div>
  </div>
</template>

<script>

export default {
  name: 'editStyle',
  props: {
    msg: String
  },
  data () {
    return {
      selectBoolean: false, // 第一次选中
      minusDisable: true,
      addDisable: false,
      acceptNumber: 0, // 选择中的数量 默认设置为1
      select: '', // 已选中的
      nowKind: [], // 当前点击选中的kind
      kinds: {}
    }
  },
  methods: {
    minus () {
      // 1是最小情况
      // eslint-disable-next-line
      this.acceptNumber > 1 ? this.acceptNumber -= 1 : ''
    },
    add () {
      if (this.acceptNumber >= 10) {
        this.$parent.$parent.showTips('单次购买不可超过十件')
      }
      const max = +this.select.kind_total_count > 10 ? 10 : +this.select.kind_total_count
      // eslint-disable-next-line
      this.acceptNumber < max ? this.acceptNumber += 1 : ''
    },
    // 选择最终结果
    selectMethod (li, e, index = 1) {
      if (li.total <= 0) {
        return
      }
      _hmt.push(['_trackEvent', 'home', 'click', `size` + index])
      const parseQuay = [...document.querySelectorAll('.size-template .styles .style')]
      parseQuay.forEach(v => {
        v.classList.remove('select-style')
      })
      e.srcElement.classList.add('select-style')
      this.select = li
      this.acceptNumber = li.kind_total_count > 0 ? 1 : 0 // 初始化 0 在这里刷新
      this.$nextTick(() => {
        this.minusDisable = true
        this.addDisable = false
      })
    },
    // 选择当前样式
    changeKind (li, e, index = 1) {
      if (li[0].total <= 0) {
        return
      }
      _hmt.push(['_trackEvent', 'home', 'click', `type` + index])
      this.selectBoolean = true
      this.nowKind = []
      this.select = null // 初始化
      console.log(
        [...document.querySelectorAll('.size-template .styles .style')]
      )
      const parseQuay = [...document.querySelectorAll('.size-template .styles .style')]
      parseQuay.forEach(v => {
        v.classList.remove('select-style')
      })
      const parseQuay1 = [...document.querySelectorAll('.union-id-1 .styles .style')]
      parseQuay1.forEach(v => {
        v.classList.remove('select-style')
      })
      e.srcElement.classList.add('select-style')
      this.nowKind = li
    },
    // 最终选中提交结果
    submitData () {
      if (this.acceptNumber <= 0) {
        return
      }
      _hmt.push(['_trackEvent', 'home', 'click', `next`])
      this.$emit('submitData', {
        count: this.acceptNumber,
        data: this.select
      })
      // eslint-disable-next-line
      this.$parent && this.$parent.hideGreenTemplate
        ? this.$parent.hideGreenTemplate()
        : ''
    }
  },
  watch: {
    acceptNumber (val, oldVal) {
      // console.log(this.select);
      (this.acceptNumber <= +this.select.kind_total_count && this.acceptNumber !== 1) ? this.minusDisable = false : this.minusDisable = true;
      (this.acceptNumber >= 1 && this.acceptNumber !== +this.select.kind_total_count) ? this.addDisable = false : this.addDisable = true
      // this.inputData.count = val
    }
  },
  mounted () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped src="./editStyle.scss">
</style>
