<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import MyInput from '../components/Input.vue'
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import editStyle from '../components/editStyle' // 用户选择款式

// [[]] 如果没有 v 那么就push一个空的进去 我记起来是用concat的 [].concat([v])
// [[v]] 查看有没有 v, 如果有[[v, v]], 如果没有 [[v], [v2]]

export default {
  name: 'home',
  data () {
    return {
      goods_id: 1, // 从url上面获取的
      goodsBaseData: {}, // 根据goods_id 获得
      greenTemplate: false, // 展示灰色背景
      editStyle: false, // 是否展示选择样式页
      nowShowInGreenTemplate: [], // greenTemplate 中 显示的组件的名称
      contentType: 1, // 中间bar的 三个选项 展示的内容开关
      home_height: 0,
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        }
      },
      selectGoods: null // 最终选中的商品
    }
  },
  components: {
    swiper,
    swiperSlide,
    editStyle
  },
  methods: {
    toRecord () {
      if (!this.$root.openid) {
        this.$root.getLogin(() => {
          this.$router.push('/record')
        })
        return
      }
      this.$router.push('/record')
    },
    parseTime (s) {
      return this.WJH.dateFormat(+(s + '000'), 'dd天hh:mm')
    },
    parseTime2 (s) {
      return this.WJH.dateFormat(+(s + '000'), 'yyyy/MM/dd hh:mm')
    },
    parseTime3 (s) {
      return this.WJH.dateFormat(+(s + '000'), 'MM月dd日hh:mm')
    },
    // 获取棉被基础信息
    getData () {
      this.goods_id = this.WJH.parseQuery(window.document.location.href).goods_id || 1
      this.$axios.get('/web/v1/cosleep/entitygoods', { params: { goods_id: this.goods_id } }).then(res => {
        if (res.data.status === 1) {
          this.goodsBaseData = res.data.data
        }
      })
    },
    // editStyle 组件中收取到的
    acceptData (data) {
      this.selectGoods = data
    },
    // 关闭 greenTemplate
    hideGreenTemplate () {
      this.greenTemplate = false
      this.nowShowInGreenTemplate.forEach(v => { this[v] = false })
    },
    // 展示 greenTemplate
    showGreenTemplate (name, boolean) {
      _hmt.push(['_trackEvent', 'home', 'click', `select`])
      this.greenTemplate = true
      this[name] = boolean
      if (this.$refs['editStyle'].selectBoolean === false) {
        console.log(this.$refs['editStyle'].selectBoolean)
        this.$nextTick(() => {
          document.querySelector('.union-id-1 .styles .style').classList.add('select-style')
        })
      }
    },
    // 获取食物商品款式
    getGoodsKind () {
      this.$root.showLoading = true
      this.$axios.get('/web/v1/cosleep/entitygoods/kind', { params: { goods_id: this.$root.goods_id } }).then(res => {
        if (res.data.status !== 1) {
          // console.log('网络错误')
          return
        }
        this.$root.showLoading = false
        let kinds = {
          '春日气息：绿色': [],
          '阳光记忆：米黄': [],
          '静晓：天蓝': []
        }
        const data = res.data.data
        data.forEach(v => {
          kinds[v.kind_style].push(v)
        })

        for (let i in kinds) {
          let total = 0
          kinds[i].forEach(v => {
            total += +v.kind_total_count
          })
          kinds[i].forEach(v => {
            v.total = total
          })
        }

        // console.log(this.$refs['editStyle'])
        this.$refs['editStyle'].kinds = kinds
        this.$refs['editStyle'].nowKind = kinds['春日气息：绿色']
      })
    },
    // 立刻下单
    submit (boolean) {
      _hmt.push(['_trackEvent', 'home', 'click', `shopping`])
      if (!this.$root.openid) {
        this.$root.getLogin(() => {
          if (boolean) {
            this.$router.push(`/edit?id=${this.selectGoods.data.kind_id}&count=${this.selectGoods.count}`)
          } else {
            this.$parent.showTips('请选择款式和尺寸')
            this.showGreenTemplate('editStyle', true)
          }
        })
        return
      }
      if (boolean) {
        this.$router.push(`/edit?id=${this.selectGoods.data.kind_id}&count=${this.selectGoods.count}`)
      } else {
        this.$parent.showTips('请选择款式和尺寸')
        this.showGreenTemplate('editStyle', true)
      }
    },
    contentTypeChange (type) {
      this.contentType = +type
    }
  },
  mounted () {
    this.getData()
    this.getGoodsKind()
  }
}
</script>

