import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
import WJH from '@/js/wjhJS.js'
import animated from 'animate.css'
import Share from '@/js/shareAndGetName'
import Tool from './js/tool.js'
import Axios from 'axios'
const goodsData = require('./assets/goods/1.json')
const area = require('./json/area.json')

const URL = process.env.NODE_ENV === 'production' && process.env.VUE_APP_TITLE !== 'experiment' ? 'https://api.psy-1.com' : 'https://api.debug.psy-1.com'

const callAppRouter = (method, params = {}, callback) => {
  try {
    let req = {
      'Method': method,
      'Data': params
    }; let cbName = `CB_${Date.now()}_${Math.ceil(Math.random() * 10)}`
    req = JSON.stringify(req)
    /* eslint-disable */
    Xinchao.Web[cbName] = (err, result) => {
      if (callback) {
        callback(err, result)
      }
      delete Xinchao.Web[cbName]
    }
    if (/(iPhone|iPad|iPod)/i.test(navigator.userAgent)) {
      window.webkit.messageHandlers.XinchaoApp.postMessage({ req, cbName })
    } else {
      /* eslint-disable */
      XinchaoApp.callRouter(req, cbName)
    }
  } catch (e) {

  }
}

const myAxios = Axios.create({
  baseURL: URL
})

Vue.prototype.WJH = WJH
Vue.prototype.$axios = myAxios
Vue.prototype.$tool = Tool
Vue.config.productionTip = false
require('@/js/setSize')
Vue.use(animated)

new Vue({
  router,
  store,
  data () {
    return {
      vip: undefined,
      url: URL,
      goodsData,
      area,
      goods_id: 1,
      openid: null,
      changeStatus: ['等待支付', '买家已付款', '订单已发货', '订单已完成', '订单已失效'],
      showLoading: false
    }
  },
  methods: {
    getOpenid (cb = () => {}) {
      callAppRouter('Login', {}, (res, ed) => {
        let mesg = null
        if (typeof ed === 'string') {
          mesg = JSON.parse(ed)
        } else {
          mesg = ed
        }
        this.openid = mesg.data.openid
        console.log('miao2', mesg.data.vip)
        this.vip = mesg.data.vip
        Vue.prototype.$axios = Axios.create({
          baseURL: URL,
          headers: {
            'openid': mesg.data.openid
          }
        })
        localStorage.setItem('openid', mesg.data.openid)
        cb()
      })
    },
    getLogin (cb) {
      callAppRouter('isLogin', {}, (res, ed) => {
        let mesg = null
        console.log('miao')
        if (typeof ed === 'string') {
          mesg = JSON.parse(ed)
        } else {
          mesg = ed
        }
        if (mesg.msg === '已登录') {
          const openid = this.getOpenid(cb)
        } else {
          callAppRouter('Login', {}, (res, ed) => {
          })
          console.log('miao3')
        }
      })
    },
    shareM (title = '小睡眠', desc = '', picUrl) {
      // eslint-disable-next-line
      const share = new Share({ pic: picUrl, url: window.location.href.split('#')[0], title: title, desc: desc })
      share.appShare()
      share.rawWeiXinShare(this.url)
    },
    test () {
      if (Tool.is_cosleep()) {
        this.share.appGetName()
      }
      if (/micromessenger/.test(navigator.userAgent.toLowerCase())) {
        this.share.weiXinInit(this.url)
        this.share.weatherCode()
        this.share.weiXinGetName(this.url)
      }
    }
  },
  mounted () {
    this.getLogin()
  },
  render: h => h(App)
}).$mount('#app')
